<template>
  <div class="container">
    <v-container class="mt-8">
        <v-layout row wrap>
            <v-layout column wrap>
                <v-flex xs1 sm1 md1 lg1 v-for="item in TabType" :key="item.name">
                    <v-btn dark text class="my-3 indigo" @click="changeType(item.Type)"><v-icon>{{item.Icon}}</v-icon></v-btn>
                </v-flex>
            </v-layout>
            <v-flex xs10 sm10 md10 lg10>
                <div class="Chart" v-if="ChartType == 'Barchart'">
                <h2 style="text-align:center;">{{Titre}}</h2>
                <bar-example/>
                </div>

                <div class="Chart" v-if="ChartType == 'Horizontal Barchart'">
                    <h2 style="text-align:center;">{{Titre}}</h2>
                    <horizontal-bar-example/>
                </div>

                <div class="Chart" v-if="ChartType == 'Doughnutchart'">
                <h2 style="text-align:center;">{{Titre}}</h2>
                <doughnut-example/>
                </div>

                <div class="Chart" v-if="ChartType == 'Piechart'">
                <h2 style="text-align:center;">{{Titre}}</h2>
                <pie-example/>
                </div>

                <div class="Chart" v-if="ChartType == 'Linechart'">
                <h2 style="text-align:center;">{{Titre}}</h2>
                <line-example/>
                </div>

            </v-flex>
            </v-layout>
    </v-container>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import BarExample from '../example/BarExample3'
  import DoughnutExample from '../example/DoughnutExample3'
  import PieExample from '../example/PieExample3'
  import HorizontalBarExample from '../example/HorizontalBarExample3'
  import LineExample from '../example/LineExample3'
  
  export default {
    components: {
      BarExample,
      DoughnutExample,
      PieExample,
      HorizontalBarExample,
      LineExample
    },
    computed: {
    ...mapState(['tabvalueoneyear']),

    },
    data() {
        return {
            TabType : [
            {Type : "Barchart",
            Icon : "mdi-chart-bar"},
            {Type : "Doughnutchart",
            Icon : "mdi-chart-donut"},
            {Type : "Piechart",
            Icon : "mdi-chart-pie"},
            {Type : "Horizontal Barchart",
            Icon : "mdi-align-horizontal-left"},
            {Type : "Linechart",
            Icon : "mdi-chart-line"},
            ],
            ChartType : "Linechart",
            Titre : "Graph"
        }
    },
    methods : {
        changeType(type) {
            this.ChartType = type
        }
    },
    watch : {
      tabvalueoneyear :{
        handler : function () {
            var nom = this.tabvalueoneyear.data[0].name
            var date = this.tabvalueoneyear.data[0].result[0][0].date.split("-")[0]
            this.Titre = "Evolution de "+ nom + " pour l'année " + date
        },
        deep : true
      },
    }, 
    mounted() {
        var nom = this.tabvalueoneyear.data[0].name
        var date = this.tabvalueoneyear.data[0].result[0][0].date.split("-")[0]
        this.Titre = "Evolution de "+ nom + " pour l'année " + date
    }
  }
</script>

